@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap");

body, ul, ol, p, h1, h2, h3, h4, h5, h6, dl, dt, dd, table, th, td {
	margin: 0;
	padding: 0;
}
body {
	line-height: 1;
	font-family: "Open Sans", "Noto Sans KR", sans-serif;
	font-size: 1em;
	color: #000;
}
ul {
	list-style: none;
}
a {
	text-decoration: none;
	color: #000;
}
img {
	vertical-align: top;
}

#start header {
	background-color: #fff;
	position: fixed;
	width: 100%;
	height: 65px;
	transition: height 0.5s;
	overflow: hidden;
	z-index: 1000;
}

#start header.active {
	height: 267px;
}

#start header .inner {
	display: flex;
	margin: 0 auto;
	max-width: 1140px;
	padding-top: 3px;
	justify-content: space-between;
	height: 65px;
}

#start header .inner .left {
	display: flex;
	gap: 40px;
	align-items: center;
}

#start header .inner .left .logo a {
	display: block;
	text-indent: -100px;
	overflow: hidden;
	width: 78px;
	height: 22px;
	background: url(./css/images/logo.png) no-repeat center;
	background-size: cover;
}

#start header .inner .right {
	display: flex;
	gap: 30px;
	align-items: center;
}

#start header .inner .right .link {
	display: block;
	text-indent: -100px;
	overflow: hidden;
	width: 83px;
	height: 14px;
	background: url(./css/images/logo_papermore_small.png) no-repeat center;
	background-size: cover;
}

#start header .inner .right .sns ul {
	display: flex;
	gap: 20px;
	align-items: center;
}

#start header .inner .right .sns ul li a img{
	vertical-align: middle;
}

#start header .inner nav {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	
	white-space: nowrap;
} 

#start > header > .inner > nav > ul {
	display: flex;
	justify-content: center;
	align-items: center;
}

#start > header > .inner > nav > ul > li {
	position: relative;
}

#start > header > .inner > nav > ul > li > a {
	display: inline-block;
	padding: 0 25px;
	line-height: 65px;
	font-weight: 500;
}

#start header .inner nav li ul {
	position: absolute;
	left: 0;
	padding-left: 25px;
	width: 100%;
}

#start header.active .inner nav li ul {
	display: block;
}

#start header .inner nav li ul li {
	line-height: 25px;
}

#start header .inner nav li ul li a {
	font-size: 0.875em;
	color: #666;
	font-weight: 500;
}

#start #main {
height: 100vh;
}

#start #main .dimmed {
	opacity: 0;
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	background-color: #000;
}

#start #main .visual {
	width: 100%;
	height: 100%;
	background: url(./css/images/main.jpg) no-repeat center;
	background-size: cover;
}
